<script>
  import { shrink } from "../../transitions/shrink.js";
  import { isNewVersion } from "../../api.js";
  import { onMount } from "svelte";
  import Icon from "svelte-awesome";
  import { bell } from "svelte-awesome/icons";

  let newVersion;
  const load = async () => {
    try {
      newVersion = await isNewVersion();
    } catch {}
  };
  onMount(load);
</script>

<style>
  .new-version {
    margin-bottom: 1rem;
    background: rgb(58, 166, 58);
    border: 1px solid green;
    color: white;
    padding: 1rem;
    display: flex;
    align-items: center;
  }
  .icon {
    display: flex;
    align-items: center;
    margin-right: 1rem;
  }
  a {
    font-weight: bolder;
  }
</style>

{#if newVersion}
  <div in:shrink class="new-version">
    <span class="icon">
      <Icon data={bell} scale={2} />
    </span>
    <div>
      ¡Hay una nueva versión disponible!
      <a
        target="_blank"
        href="https://github.com/nilbelec/potatorrent/releases">
        Descárgala aquí
      </a>
    </div>
  </div>
{/if}
