<script>
  import { scale } from "svelte/transition";

  const duration = 280;
  let inProps = { y: -1000, duration: duration };
  let outProps = { duration: duration, y: 1000 };
</script>

<style>
  .animated {
    position: absolute;
    width: 100%;
  }
</style>

<div class="animated" in:scale={inProps} out:scale={outProps}>
  <slot />
</div>
